.carousel-screen{
  max-width: 85%;
  margin: auto;
}

@media (max-width: 800px){
  .carousel-screen{
    display: none;
  }
}

@media (min-width: 800px){
  .carousel-screen{
    display: block;
  }
}

.why__container{
  padding: 2em;
  background: #eee;
  -webkit-transform: rotate(-7.6deg);
      -ms-transform: rotate(-7.6deg);
          transform: rotate(-7.6deg);
  width: 140%;
  position: relative;
  left: -180px;
  margin-bottom: 5em;
}

.why{
  max-width: 600px;
  margin: auto;
  -webkit-transform: rotate(7.6deg);
      -ms-transform: rotate(7.6deg);
          transform: rotate(7.6deg);
  padding: 1em;
}
.carousel__contain{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.carousel__contain--info{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 3 1;
      flex: 3 1;
  max-width: 350px;
}
.carousel__contain--info h2{
  margin: 0;
}

.carousel__contain--carousel{
  max-width: 350px;
  -ms-flex: 4 1;
      flex: 4 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  max-height: 184px;
}






